import jss from 'jss';
import jssGlobal from 'jss-plugin-global';
import jssNested from 'jss-plugin-nested';
import { ParameterTreeComboBox } from './tree_combo_box';

export function setTreeComboBoxStyle({ opacity, background, color, border, ['border-radius']: borderRadius }: {
    opacity?: number;
    background?: string;
    color?: string;
    border?: string;
    'border-radius'?: string;
}) {
    jss.use(jssGlobal(), jssNested());

    const cls = `${ParameterTreeComboBox.BASE_CLS}-${BI.UUID()}`;

    const selector = `.${cls}`;

    jss.createStyleSheet({
        '@global': {
            [`${selector}`]: {
                opacity,
                background,
            },
            [`${selector} .bi-card`]: {
                background,
                color,
            },
            [`${selector} .bi-text`]: {
                color,
            },
            [`${selector} .bi-trigger`]: {
                color,
            },
            [`${selector} .bi-icon-button`]: {
                color,
            },
            [`${selector} .bi-border`]: {
                border,
                'boder-radius': borderRadius,
            },
        },
    }).attach();

    return cls;
}
